@font-face {
  font-family: "Montserrat Alternates";
  src: local("montserrat-alternates-regular"), url("../fonts/montserrat-alternates-regular.woff2") format("woff2"), url("../fonts/montserrat-alternates-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Alternates";
  src: local("montserrat-alternates-medium"), url("../fonts/montserrat-alternates-medium.woff2") format("woff2"), url("../fonts/montserrat-alternates-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Alternates";
  src: local("montserrat-alternates-semibold"), url("../fonts/montserrat-alternates-semibold.woff2") format("woff2"), url("../fonts/montserrat-alternates-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Alternates";
  src: local("montserrat-alternates-bold"), url("../fonts/montserrat-alternates-bold.woff2") format("woff2"), url("../fonts/montserrat-alternates-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Alternates";
  src: local("montserrat-alternates-extrabold"), url("../fonts/montserrat-alternates-extrabold.woff2") format("woff2"), url("../fonts/montserrat-alternates-extrabold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Alternates";
  src: local("montserrat-alternates-black"), url("../fonts/montserrat-alternates-black.woff2") format("woff2"), url("../fonts/montserrat-alternates-black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-10px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(10px);
  }
}
.page {
  height: 100%;
  scroll-behavior: smooth;
  background-color: #1d1d1e;
}

.page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
.page__body--hidden {
  overflow: hidden;
}

.header {
  flex: 0 0 auto;
}

.header__wrapper {
  display: flex;
  max-width: 1280px;
  padding: 0 49px;
  margin: 0 auto;
  min-height: 59px;
  align-items: center;
}
@media (max-width: 1279px) {
  .header__wrapper {
    justify-content: space-between;
  }
}
@media (max-width: 1023px) {
  .header__wrapper {
    background-color: #8c8c8c;
    min-height: 104px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    min-height: 80px;
    min-height: 44px;
  }
}

.header__nav {
  display: flex;
}

.header__logo {
  margin-right: 50px;
}

.nav {
  position: relative;
}
@media (max-width: 1279px) {
  .nav {
    width: 300px;
  }
}
@media (max-width: 1023px) {
  .nav {
    width: 490px;
  }
}
@media (max-width: 767px) {
  .nav {
    width: 348px;
  }
}

.nav__list,
.nav__contact-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list {
  margin-right: 70px;
}
@media (max-width: 1279px) {
  .nav__list {
    flex-direction: column;
    margin: 0;
    align-items: center;
    background: linear-gradient(180deg, #fbdd51 0%, #E8BC00 100%);
    width: 450px;
    min-height: 300px;
    padding-top: 60px;
    position: absolute;
    right: 0;
    top: -10px;
  }
  .nav__list .nav__item a {
    color: #1d1d1e;
  }
}
@media (max-width: 1023px) {
  .nav__list {
    width: 100%;
    height: 100%;
  }
}

@media (max-width: 1279px) {
  .nav__contact-list {
    order: -1;
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .nav__contact-list img {
    display: none;
  }
}

.nav__wrapper {
  display: flex;
  align-items: center;
}
@media (max-width: 1279px) {
  .nav__wrapper {
    width: 100%;
    height: 100%;
    opacity: 0;
    visibility: hidden;
    flex-direction: column;
    position: fixed;
    z-index: 1;
    right: 0;
    background-color: rgba(29, 29, 30, 0.5);
  }
  .nav__wrapper--show {
    opacity: 1;
    transition: 0.3s ease-in-out;
    visibility: visible;
    overflow: auto;
  }
}
@media (max-width: 1279px) and (max-width: 1023px) {
  .nav__wrapper--show {
    width: 100%;
    top: 0;
    position: fixed;
    height: 100%;
  }
}

.nav__item {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 400;
  font-style: normal;
  padding: 4px 15px;
}
@media (max-width: 1279px) {
  .nav__item {
    margin-bottom: 10px;
  }
}
.nav__item a {
  color: #ffffff;
}
@media (min-width: 1280px) {
  .nav__item:hover, .nav__item:focus-visible {
    outline: 1px solid #fed524;
    transition: 0.3s ease-in-out;
  }
  .nav__item:hover a, .nav__item:focus-visible a {
    color: #fed524;
    transition: 0.3s ease-in-out;
  }
}

.nav__contact-item {
  margin-right: 8px;
  transition: 0.3s ease-in-out;
}
@media (max-width: 1279px) {
  .nav__contact-item:last-child {
    margin-right: 0;
  }
}
.nav__contact-item a {
  display: flex;
  align-items: center;
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
}
.nav__contact-item span {
  padding-left: 7px;
}
.nav__contact-item:hover span, .nav__contact-item:focus-visible span {
  transition: 0.3s ease-in-out;
  color: #fed524;
}

.main {
  flex: 1 0 auto;
  overflow: hidden;
  transition: 0.3s ease-in-out;
}

.intro {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 0 100px 0;
  min-height: 605px;
  position: relative;
}
@media (max-width: 1023px) {
  .intro {
    min-height: 1128px;
  }
}
@media (max-width: 767px) {
  .intro {
    min-height: 686px;
  }
}
.intro::before {
  position: absolute;
  content: "";
  background-image: url("../img/decor/decor-intro-money.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 832px;
  height: 366px;
  right: calc((100% - 1681px) / 2);
  bottom: 91px;
  z-index: -1;
  transform: rotate3d(0, 1, 0, 180deg);
}
@media (max-width: 1023px) {
  .intro::before {
    right: calc((100% - 838px) / 2);
    bottom: 139px;
  }
}
@media (max-width: 767px) {
  .intro::before {
    right: calc((100% - 356px) / 2);
    bottom: 70px;
    width: 353px;
    height: 164px;
  }
}
.intro::after {
  position: absolute;
  content: url("../img/decor/decor-intro-backlight.svg");
  z-index: -2;
  bottom: -50px;
  right: calc((100% - 1800px) / 2);
}
@media (max-width: 1023px) {
  .intro::after {
    right: calc((100% - 1214px) / 2);
  }
}
@media (max-width: 767px) {
  .intro::after {
    right: calc((100% - 1067px) / 2);
    bottom: -171px;
  }
}

.intro__wrapper {
  max-width: 711px;
  width: 100%;
  padding-top: 103px;
}
@media (max-width: 1023px) {
  .intro__wrapper {
    padding-top: 70px;
  }
}
@media (max-width: 767px) {
  .intro__wrapper {
    padding-top: 38px;
  }
}
.intro__wrapper button {
  margin-top: 25px;
}
@media (max-width: 767px) {
  .intro__wrapper button {
    margin-top: 10px;
  }
}
.intro__wrapper h1,
.intro__wrapper p {
  margin: 0;
}
.intro__wrapper h1 {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 50px;
  line-height: 61px;
  font-weight: 900;
  font-style: normal;
  color: #fed524;
  padding-bottom: 37px;
}
@media (max-width: 1023px) {
  .intro__wrapper h1 {
    padding-bottom: 62px;
  }
}
@media (max-width: 767px) {
  .intro__wrapper h1 {
    font-size: 26px;
    line-height: 32px;
    padding-bottom: 23px;
  }
}
.intro__wrapper::before {
  position: absolute;
  content: "";
  background-image: url("../img/decor/decor-intro-diagram.svg");
  background-size: cover;
  background-repeat: no-repeat;
  width: 1029px;
  height: 454px;
  opacity: 0.25;
  z-index: -3;
  right: calc((100% - 1788px) / 2);
  bottom: 142px;
}
@media (max-width: 1023px) {
  .intro__wrapper::before {
    right: calc((100% - 957px) / 2);
    width: 901px;
    height: 473px;
    bottom: 147px;
  }
}
@media (max-width: 767px) {
  .intro__wrapper::before {
    width: 386px;
    height: 162px;
    bottom: 119px;
    right: calc((100% - 406px) / 2);
  }
}

.intro__desc {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 28px;
  line-height: 34px;
  font-weight: 500;
  font-style: normal;
  color: #ffffff;
  padding-bottom: 34px;
  padding-right: 50px;
}
@media (max-width: 1023px) {
  .intro__desc {
    padding-bottom: 42px;
  }
}
@media (max-width: 767px) {
  .intro__desc {
    font-size: 18px;
    line-height: 22px;
    padding-bottom: 15px;
    padding-right: 30px;
  }
}
.intro__desc span {
  font-size: 20px;
  line-height: 24px;
  color: #fed524;
}
@media (max-width: 767px) {
  .intro__desc span {
    font-size: 16px;
    line-height: 20px;
  }
}

.about {
  position: relative;
}
.about::before {
  position: absolute;
  content: "";
  background-image: url("../img/decor/decor-coin-two.svg");
  background-size: cover;
  width: 254px;
  height: 255px;
  top: -278px;
  right: -161px;
  z-index: -1;
}
@media (max-width: 1023px) {
  .about::before {
    top: -149px;
    right: -39px;
    width: 189px;
    height: 190px;
  }
}
@media (max-width: 767px) {
  .about::before {
    display: none;
  }
}
.about::after {
  position: absolute;
  content: "";
  background-image: url("../img/decor/decor-coin-one.svg");
  background-size: cover;
  width: 121px;
  height: 150px;
  left: -158px;
  bottom: -136px;
}
@media (max-width: 1023px) {
  .about::after {
    width: 116px;
    height: 137px;
    left: -31px;
    bottom: -160px;
  }
}

.about__wrapper {
  position: relative;
  z-index: 2;
  overflow: hidden;
  background: linear-gradient(180deg, #fbdd51 0%, #E8BC00 100%);
  min-height: 966px;
  margin-top: 228px;
  margin-bottom: 131px;
  padding: 60px 0 124px;
}
@media (max-width: 1023px) {
  .about__wrapper {
    margin-top: 0;
    padding-top: 71px;
    padding-bottom: 120px;
  }
}
@media (max-width: 767px) {
  .about__wrapper {
    padding-top: 40px;
    padding-bottom: 54px;
    margin-bottom: 36px;
  }
}
.about__wrapper::before {
  position: absolute;
  content: url("../img/decor/decor-about-big.svg");
  z-index: -2;
  left: -248px;
  top: -313px;
}
.about__wrapper::after {
  position: absolute;
  content: url("../img/decor/decor-about-small.svg");
  z-index: -2;
  right: 0;
  bottom: 0;
}

.about__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 946px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .about__inner {
    padding: 0 10px;
  }
}
.about__inner h2,
.about__inner h3,
.about__inner p {
  margin: 0;
}
.about__inner h2 {
  margin: 0;
  color: #1d1d1e;
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 48px;
  line-height: 59px;
  font-weight: 800;
  font-style: normal;
  padding-bottom: 34px;
}
@media (max-width: 1023px) {
  .about__inner h2 {
    padding-bottom: 49px;
  }
}
@media (max-width: 767px) {
  .about__inner h2 {
    font-size: 26px;
    line-height: 32px;
    padding-bottom: 30px;
  }
}

.about__descr {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
  font-style: normal;
  color: #1d1d1e;
  padding: 0 10px 55px;
  letter-spacing: -0.1px;
  text-align: center;
}
@media (max-width: 1023px) {
  .about__descr {
    font-size: 16px;
    line-height: 22px;
    padding: 0 0px 72px;
    letter-spacing: -0.1px;
  }
}
@media (max-width: 767px) {
  .about__descr {
    text-align: left;
    font-size: 14px;
    line-height: 20px;
    padding-bottom: 30px;
  }
}

.about__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
  gap: 30px;
}
@media (max-width: 1023px) {
  .about__list {
    grid-template-columns: 1fr;
    row-gap: 25px;
  }
}
@media (max-width: 767px) {
  .about__list {
    padding: 0 3px;
    row-gap: 13px;
  }
}

.about__list-item {
  width: 100%;
  min-height: 194px;
  padding: 30px 26px 33px;
  background-color: #333333;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 1023px) {
  .about__list-item {
    min-height: 166px;
    padding: 38px 103px 26px;
  }
  .about__list-item br {
    display: none;
  }
}
@media (max-width: 767px) {
  .about__list-item {
    min-height: 166px;
    padding: 22px 5px 26px;
    justify-content: center;
  }
  .about__list-item br {
    display: block;
  }
}
.about__list-item h3 {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 23px;
  line-height: 28px;
  font-weight: 800;
  font-style: normal;
  color: #fed526;
  padding-bottom: 15px;
  text-align: center;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .about__list-item h3 {
    font-size: 18px;
    line-height: 22px;
    padding-bottom: 19px;
  }
}
.about__list-item p {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
  font-style: normal;
  color: #ffffff;
  text-align: center;
}
@media (max-width: 1023px) {
  .about__list-item p {
    font-size: 16px;
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .about__list-item p {
    font-size: 14px;
    line-height: 17px;
  }
}

.audit {
  min-height: 565px;
  padding-top: 68px;
  position: relative;
}
@media (max-width: 1023px) {
  .audit {
    padding-top: 89px;
    min-height: 831px;
  }
}
@media (max-width: 767px) {
  .audit {
    min-height: 576px;
  }
}
.audit::before {
  position: absolute;
  content: "";
  background-image: url("../img/decor/decor-grid-bcg.svg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 757px;
  height: 565px;
  top: 0;
  left: calc((100% - 1657px) / 2);
}
@media (max-width: 1023px) {
  .audit::before {
    width: 831px;
    height: 620px;
    transform: rotate(90deg);
    top: 146px;
    left: -177px;
  }
}
@media (max-width: 767px) {
  .audit::before {
    display: none;
  }
}

.audit__wrapper {
  background-color: #333333;
  min-height: 345px;
  position: relative;
  background-image: url("../img/decor/decor-audit-backlight.svg");
  background-repeat: no-repeat;
  background-position: -144px -246px;
}
.audit__wrapper::before {
  position: absolute;
  content: "";
  background-image: url("../img/audit/audit-img.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 260px;
  height: 336px;
  z-index: 2;
  right: calc((100% - 859px) / 2);
  bottom: -15px;
}
@media (max-width: 1023px) {
  .audit__wrapper::before {
    right: 96px;
  }
}
@media (max-width: 767px) {
  .audit__wrapper::before {
    width: 176px;
    height: 244px;
    bottom: -10px;
    right: 7px;
  }
}
.audit__wrapper h2,
.audit__wrapper p {
  margin: 0;
}
.audit__wrapper h2 {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 48px;
  line-height: 59px;
  font-weight: 700;
  font-style: normal;
  color: #e8be05;
  padding-bottom: 21px;
  max-width: 543px;
  width: 100%;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .audit__wrapper h2 {
    padding-bottom: 46px;
  }
}
@media (max-width: 767px) {
  .audit__wrapper h2 {
    font-size: 26px;
    line-height: 32px;
    padding-bottom: 30px;
    padding-left: 7px;
  }
}
.audit__wrapper p {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 18px;
  line-height: 22px;
  font-weight: 800;
  font-style: normal;
  color: #ffffff;
  max-width: 499px;
  padding-bottom: 37px;
}
@media (max-width: 1023px) {
  .audit__wrapper p {
    padding-bottom: 49px;
  }
}
@media (max-width: 767px) {
  .audit__wrapper p {
    font-size: 16px;
    line-height: 24px;
    padding-left: 10px;
    padding-bottom: 30px;
  }
}

.audit__inner {
  max-width: 1165px;
  margin: 0 auto;
  padding: 60px 0 60px 93px;
  position: relative;
  z-index: 1;
}
@media (max-width: 1023px) {
  .audit__inner {
    min-height: 686px;
    padding: 87px 0 60px 50px;
  }
}
@media (max-width: 767px) {
  .audit__inner {
    padding: 53px 5px;
    min-height: 576px;
  }
}
.audit__inner::before, .audit__inner::after {
  position: absolute;
