  height: 225px;
}
@media (max-width: 1023px) {
  .audit__inner::after {
    right: 296px;
  }
}
@media (max-width: 767px) {
  .audit__inner::after {
    width: 76px;
    height: 99px;
    right: 229px;
  }
}

.solved-tasks {
  position: relative;
}
.solved-tasks::after, .solved-tasks::before {
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  background-size: cover;
