.header {
  flex: 0 0 auto;
}

.header__wrapper {
  display: flex;
  max-width: 1280px;
  padding: 0 49px;
  margin: 0 auto;
  min-height: 59px;
  align-items: center;

  @media (max-width: 1279px) {
    justify-content: space-between;
  }

  @media (max-width: $tablet-width) {
    background-color: #8c8c8c;
    min-height: 104px;
  }

  @media (max-width: $mobile-width) {
    min-height: 80px;
    min-height: 44px;
  }
}

.header__nav {
  display: flex;
}

.header__logo {
  margin-right: 50px;
}
